footer {
  color: var(--white);
}

.footer1-bg {
  background-color: var(--very-dk-blue-opac);
}

.footer1 {
  font-family: var(--font-headings);
}
.footer1 > * {
  flex: 1;
}
@media (min-width: 40em) {
  .footer1 {
    text-align: left;
    flex-direction: row;
    gap: 12.5rem;
  }
}
@media (max-width: 71.8125em) {
  .footer1__signup {
    margin-bottom: 0.625rem;
  }
}
.footer1__label {
  display: none;
}
.footer1__email {
  width: 90%;
  border: none;
  border-radius: 0.125rem;
  padding: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer1__email {
    margin-bottom: 0.625rem;
    width: 100%;
    padding: 0.9375rem;
  }
}
.footer1__submit {
  width: 90%;
  background-color: var(--bright-blue);
  padding: 0.625rem;
  border: none;
  border-radius: 0.125rem;
  cursor: pointer;
  color: var(--white);
  font-weight: bold;
  transition: background-color 200ms ease-in-out;
}
.footer1__submit:hover {
  background-color: #537ff9;
}
@media (min-width: 71.875em) {
  .footer1__submit {
    width: 40%;
    padding: 0.9375rem;
  }
}

.footer2-bg {
  background-color: var(--very-dk-blue);
}

.footer2 {
  text-align: left;
}
.footer2 a {
  color: var(--white);
}
.footer2 ul {
  margin: 0;
}
.footer2-list {
  list-style-type: none;
  list-style-position: outside;
  padding-left: 0;
  text-align: left;
  line-height: 2;
}
.footer2__phone::before {
  content: url("../images/icon-email.svg");
  margin-right: 0.625rem;
}
.footer2__email::before {
  content: url("../images/icon-phone.svg");
  margin-right: 0.625rem;
}
.footer2-icon {
  gap: 1.25rem;
  list-style-type: none;
  list-style-position: outside;
  padding-left: 0;
}
.footer2-icon li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.875rem;
  height: 1.875rem;
  border: 0.0625rem solid;
  border-radius: 50%;
  overflow: hidden;
}
.footer2-icon li a {
  display: inline-block;
  padding: 0.625rem;
}